  margin-top: 10px;
}
.dnd-poly-drag-image{
 .item{
  //  visibility: visible;
  //  background: red;
 }
}
.dnd-poly-drag-image {
    opacity: 1 !important;
}


.item {
  @box-size:floor((100vw-10)/4);
  // position: absolute;
  // border: 1px solid #888888;
  position: relative;
  // margin-right: 10px;
  margin-top: 10px;
  box-shadow: 0px 0px 2px #888888;
  font-size: 12px;
  // width: 80px;
  // height: 80px;
  width: @box-size;
  height: @box-size;
  text-align: center;
  box-sizing: border-box;
  background: #ffffff;
  /* border-radius:90px; */
  &.on-drag{
  opacity: 0;
  &.on-over{
    opacity: 1;
  }
}
  .image-box {
    padding: 10px;
